@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&family=Space+Grotesk:wght@400;500;600;700&family=DM+Sans:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@400;500;600;700&family=DM+Sans:wght@400;500;700&display=swap);
/* Modern Font Import */

/* Rest of your CSS */
body {
  font-family: 'DM Sans', sans-serif;
  /* ... */
}
/* Modern Color Palette */
:root {
    --primary: #6366f1;  /* Indigo */
    --primary-light: #818cf8;
    --primary-dark: #4f46e5;
    --secondary: #f59e0b;  /* Amber */
    --secondary-light: #fbbf24;
    --secondary-dark: #d97706;
    --accent: #10b981;  /* Emerald */
    --accent-light: #34d399;
    --accent-dark: #059669;
    --dark: #1e293b;  /* Slate */
    --darker: #0f172a;
    --light: #f8fafc;
    --gray: #94a3b8;
    --gray-light: #e2e8f0;
    --danger: #ef4444;
    --success: #10b981;
    
    --glass: rgba(255, 255, 255, 0.08);
    --glass-border: rgba(255, 255, 255, 0.15);
    --shadow: 0 8px 32px rgba(0, 0, 0, 0.12);
    --transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
}

body {
    font-family: 'Inter', sans-serif;
    color: var(--light);
    background-color: var(--darker);
    line-height: 1.6;
    overflow-x: hidden;
}

h1, h2, h3, h4 {
    font-family: 'Space Grotesk', sans-serif;
    font-weight: 700;
}

a {
    text-decoration: none;
    color: inherit;
}

.button {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 0.8rem 1.8rem;
    background: var(--primary);
    color: var(--light);
    border-radius: 8px;
    font-weight: 600;
    transition: var(--transition);
    border: none;
    cursor: pointer;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    box-shadow: 0 4px 6px -1px rgba(99, 102, 241, 0.3), 0 2px 4px -1px rgba(99, 102, 241, 0.2);
    font-family: 'DM Sans', sans-serif;
}

.button:hover {
    transform: translateY(-2px);
    background: var(--primary-light);
    box-shadow: 0 10px 15px -3px rgba(99, 102, 241, 0.3), 0 4px 6px -2px rgba(99, 102, 241, 0.2);
}

.button.outline {
    background: transparent;
    border: 2px solid var(--primary);
    color: var(--primary);
    box-shadow: none;
}

.button.outline:hover {
    background: rgba(99, 102, 241, 0.1);
    color: var(--primary-light);
    border-color: var(--primary-light);
}

.glass-card {
    background: var(--glass);
    backdrop-filter: blur(12px);
    -webkit-backdrop-filter: blur(12px);
    border-radius: 12px;
    border: 1px solid var(--glass-border);
    box-shadow: var(--shadow);
    transition: var(--transition);
    padding: 2rem;
    overflow: hidden;
}

.glass-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

.section {
    padding: 6rem 0;
    position: relative;
}

.section-header {
    text-align: center;
    margin-bottom: 3rem;
}

.section-header h2 {
    font-size: 2.5rem;
    margin-bottom: 1rem;
    background: linear-gradient(to right, var(--primary), var(--accent));
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
    display: inline-block;
}

.divider {
    width: 80px;
    height: 4px;
    background: linear-gradient(to right, var(--primary), var(--accent));
    margin: 0 auto;
    border-radius: 2px;
}

/* Loading Screen */
.loading-screen {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: var(--darker);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
    background: linear-gradient(135deg, var(--darker), var(--dark));
}

.loading-content {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 2rem;
    gap: 2rem;
    max-width: 500px;
    width: 90%;
}

.loading-spinner {
    width: 100px;
    height: 100px;
    position: relative;
    border-radius: 50%;
}

.spinner-core {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: 5px solid transparent;
    border-top-color: var(--primary);
    border-bottom-color: var(--accent);
    animation: spin 1.5s linear infinite;
}

.spinner-outer {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: 5px solid transparent;
    border-left-color: var(--secondary);
    border-right-color: var(--primary-light);
    animation: spin 2s linear infinite reverse;
    opacity: 0.7;
}

.loading-text {
    font-size: 2.5rem;
    font-weight: 700;
    font-family: 'Space Grotesk', sans-serif;
    color: var(--light);
    position: relative;
}

.loading-text::after {
    content: '';
    position: absolute;
    bottom: -10px;
    left: 50%;
    transform: translateX(-50%);
    width: 100px;
    height: 4px;
    background: linear-gradient(to right, var(--primary), var(--accent));
    border-radius: 2px;
}

.loading-subtext {
    color: var(--gray);
    font-size: 1.1rem;
    font-family: 'DM Sans', sans-serif;
    letter-spacing: 0.5px;
}

.progress-track {
    width: 100%;
    height: 6px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 3px;
    overflow: hidden;
    margin-top: 1rem;
}

.progress-bar {
    height: 100%;
    width: 0;
    background: linear-gradient(to right, var(--primary), var(--accent));
    animation: loadingProgress 2.5s cubic-bezier(0.4, 0, 0.2, 1) forwards;
}

@keyframes spin {
    to { transform: rotate(360deg); }
}

@keyframes loadingProgress {
    0% { width: 0; }
    100% { width: 100%; }
}

/* Particles */
.particles {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    overflow: hidden;
}

.particle {
    position: absolute;
    background: rgba(255, 255, 255, 0.6);
    border-radius: 50%;
    filter: blur(1px);
    animation: floatParticle linear infinite;
    opacity: 0;
}

@keyframes floatParticle {
    0% {
        transform: translateY(0) rotate(0deg);
        opacity: 0;
    }
    10% {
        opacity: 0.6;
    }
    90% {
        opacity: 0.6;
    }
    100% {
        transform: translateY(-100vh) rotate(360deg);
        opacity: 0;
    }
}

/* Main Container */
.container {
    position: relative;
    min-height: 100vh;
    overflow: hidden;
}

.gradient-background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(135deg, var(--darker) 0%, var(--dark) 100%);
    z-index: -2;
}

.main-content {
    padding: 8rem 5% 0;
    position: relative;
}

/* Header */
.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    padding: 1.5rem 5%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 100;
    background: rgba(15, 23, 42, 0.8);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    transition: var(--transition);
}

.header.scrolled {
    padding: 1rem 5%;
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

.menu-toggle {
    font-size: 1.8rem;
    color: var(--light);
    cursor: pointer;
    transition: var(--transition);
    z-index: 101;
    display: none;
    width: 50px;
    height: 50px;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
}

.menu-toggle:hover {
    color: var(--primary-light);
    transform: rotate(90deg);
}

.logo {
    width: 60px;
    height: 60px;
    border-radius: 12px;
    overflow: hidden;
    border: 2px solid var(--light);
    box-shadow: var(--shadow);
    transition: var(--transition);
}

.logo:hover {
    transform: scale(1.05);
    border-color: var(--primary-light);
    box-shadow: 0 0 30px rgba(99, 102, 241, 0.5);
}

.logo img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.header-links {
    display: flex;
    grid-gap: 2rem;
    gap: 2rem;
}

.header-links a {
    position: relative;
    font-weight: 500;
    transition: var(--transition);
    padding: 0.5rem 0;
    font-family: 'DM Sans', sans-serif;
}

.header-links a::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0;
    height: 2px;
    background: var(--primary-light);
    transition: var(--transition);
}

.header-links a:hover::after {
    width: 100%;
}

.mobile-social {
    display: none;
    grid-gap: 1.5rem;
    gap: 1.5rem;
    margin-top: 2rem;
    justify-content: center;
}

.mobile-social a {
    font-size: 1.5rem;
    color: var(--light);
    transition: var(--transition);
}

.mobile-social a:hover {
    color: var(--primary-light);
    transform: translateY(-3px);
}

/* Hero Section */
.hero-section {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 5rem 0;
    position: relative;
}

.hero-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 3rem;
    gap: 3rem;
    width: 100%;
}

.profile-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 2rem;
    gap: 2rem;
}

.profile-photo {
    width: 280px;
    height: 280px;
    border-radius: 16px;
    overflow: hidden;
    position: relative;
    box-shadow: var(--shadow);
    animation: float 6s ease-in-out infinite;
}

.photo-border {
    position: absolute;
    inset: -10px;
    border-radius: 16px;
    border: 3px solid transparent;
    border-top-color: var(--primary);
    border-bottom-color: var(--accent);
    animation: rotate 8s linear infinite;
    z-index: -1;
}

.profile-photo img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.5s ease;
}

.profile-photo:hover img {
    transform: scale(1.05);
}

.profile-info {
    text-align: center;
}

.profile-info h1 {
    font-size: 3rem;
    margin-bottom: 0.5rem;
    color: var(--light);
    line-height: 1.2;
}

.profile-info .title {
    font-size: 1.2rem;
    color: var(--gray);
    margin-bottom: 1.5rem;
    font-family: 'DM Sans', sans-serif;
}

.social-links {
    display: flex;
    grid-gap: 1.5rem;
    gap: 1.5rem;
    justify-content: center;
}

.social-links a {
    width: 50px;
    height: 50px;
    border-radius: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(99, 102, 241, 0.1);
    border: 1px solid rgba(99, 102, 241, 0.2);
    transition: var(--transition);
    font-size: 1.3rem;
    color: var(--light);
}

.social-links a:hover {
    transform: translateY(-5px);
    background: rgba(99, 102, 241, 0.2);
    color: var(--primary-light);
    box-shadow: 0 10px 15px -3px rgba(99, 102, 241, 0.3);
}

.scroll-down {
    position: absolute;
    bottom: 2rem;
    left: 50%;
    transform: translateX(-50%);
    color: var(--light);
    font-size: 1.5rem;
    animation: bounce 2s infinite;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background: rgba(99, 102, 241, 0.1);
    border: 1px solid rgba(99, 102, 241, 0.2);
}

@keyframes float {
    0%, 100% { transform: translateY(0) rotate(0deg); }
    25% { transform: translateY(-20px) rotate(2deg); }
    50% { transform: translateY(-10px) rotate(-2deg); }
    75% { transform: translateY(-15px) rotate(2deg); }
}

@keyframes rotate {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

@keyframes bounce {
    0%, 20%, 50%, 80%, 100% { transform: translateY(0) translateX(-50%); }
    40% { transform: translateY(-20px) translateX(-50%); }
    60% { transform: translateY(-10px) translateX(-50%); }
}

/* About Section */
.about-content {
    display: flex;
    grid-gap: 3rem;
    gap: 3rem;
    align-items: center;
}

.about-text {
    flex: 2 1;
}

.about-text p {
    margin-bottom: 1.5rem;
    color: var(--gray);
    font-size: 1.1rem;
    line-height: 1.7;
}

.about-actions {
    display: flex;
    grid-gap: 1.5rem;
    gap: 1.5rem;
    margin-top: 2rem;
}

.about-stats {
    flex: 1 1;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.stat-item {
    background: rgba(99, 102, 241, 0.1);
    padding: 1.5rem;
    border-radius: 12px;
    text-align: center;
    transition: var(--transition);
    border: 1px solid rgba(99, 102, 241, 0.2);
}

.stat-item:hover {
    transform: translateY(-5px);
    background: rgba(99, 102, 241, 0.2);
    box-shadow: 0 10px 15px -3px rgba(99, 102, 241, 0.1);
}

.stat-item h3 {
    font-size: 2.5rem;
    color: var(--primary-light);
    margin-bottom: 0.5rem;
    font-family: 'Space Grotesk', sans-serif;
}

.stat-item p {
    color: var(--gray);
    font-size: 0.9rem;
    font-family: 'DM Sans', sans-serif;
}

/* Projects Section */
.projects-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    grid-gap: 2rem;
    gap: 2rem;
}

.project-card {
    display: flex;
    flex-direction: column;
    transition: var(--transition);
    border-radius: 12px;
    overflow: hidden;
}

.project-image {
    position: relative;
    border-radius: 12px 12px 0 0;
    overflow: hidden;
    height: 200px;
}

.project-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.5s ease;
}

.project-card:hover .project-image img {
    transform: scale(1.1);
}

.project-tags {
    position: absolute;
    bottom: 1rem;
    left: 1rem;
    display: flex;
    grid-gap: 0.5rem;
    gap: 0.5rem;
}

.tag {
    background: rgba(15, 23, 42, 0.9);
    color: var(--light);
    padding: 0.3rem 0.8rem;
    border-radius: 50px;
    font-size: 0.8rem;
    font-family: 'DM Sans', sans-serif;
    font-weight: 500;
}

.project-info {
    padding: 1.5rem;
    flex: 1 1;
    display: flex;
    flex-direction: column;
}

.project-info h3 {
    margin-bottom: 1rem;
    font-size: 1.3rem;
    color: var(--light);
}

.project-description {
    color: var(--gray);
    margin-bottom: 1.5rem;
    flex: 1 1;
    font-size: 0.95rem;
}

.technologies {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    margin-bottom: 1.5rem;
}

.tech {
    background: rgba(16, 185, 129, 0.1);
    color: var(--accent-light);
    padding: 0.3rem 0.8rem;
    border-radius: 50px;
    font-size: 0.8rem;
    font-family: 'DM Sans', sans-serif;
    font-weight: 500;
}

/* Skills Section */
.skills-filter {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 2rem;
}

.filter-btn {
    padding: 0.5rem 1.5rem;
    border-radius: 50px;
    background: transparent;
    border: 1px solid var(--primary);
    color: var(--primary);
    cursor: pointer;
    transition: var(--transition);
    font-family: 'DM Sans', sans-serif;
    font-weight: 500;
}

.filter-btn:hover, .filter-btn.active {
    background: var(--primary);
    color: var(--light);
    transform: translateY(-2px);
}

.skills-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.skill-item {
    display: flex;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
    padding: 1.5rem;
    transition: var(--transition);
    border-radius: 12px;
}

.skill-item.active, .skill-item:hover {
    background: rgba(99, 102, 241, 0.1);
    transform: translateY(-5px);
}

.skill-icon {
    font-size: 2rem;
    width: 60px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 12px;
    background: rgba(99, 102, 241, 0.1);
    color: var(--primary-light);
}

.skill-info {
    flex: 1 1;
}

.skill-info h4 {
    margin-bottom: 0.5rem;
    color: var(--light);
}

.skill-progress {
    width: 100%;
    background: rgba(255, 255, 255, 0.1);
    height: 6px;
    border-radius: 3px;
    position: relative;
    margin-top: 0.5rem;
}

.skill-progress .progress-bar {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    background: linear-gradient(to right, var(--primary), var(--accent));
    border-radius: 3px;
    transition: width 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

.skill-progress span {
    position: absolute;
    right: 0;
    top: -25px;
    font-size: 0.8rem;
    color: var(--gray);
    font-family: 'DM Sans', sans-serif;
}

/* Contact Section */
.contact-container {
    display: flex;
    grid-gap: 3rem;
    gap: 3rem;
    border-radius: 12px;
}

.contact-info {
    flex: 1 1;
    padding-right: 2rem;
}

.contact-info h3 {
    font-size: 1.8rem;
    margin-bottom: 1.5rem;
    color: var(--light);
}

.contact-info p {
    color: var(--gray);
    margin-bottom: 2rem;
    line-height: 1.7;
}

.contact-methods {
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
}

.contact-link {
    display: flex;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
    color: var(--primary-light);
    transition: var(--transition);
    font-family: 'DM Sans', sans-serif;
}

.contact-link:hover {
    color: var(--accent-light);
    transform: translateX(5px);
}

.contact-form {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.form-group {
    position: relative;
}

.form-group input,
.form-group textarea {
    width: 100%;
    padding: 1rem;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 8px;
    color: var(--light);
    font-family: 'DM Sans', sans-serif;
    transition: var(--transition);
}

.form-group input::placeholder,
.form-group textarea::placeholder {
    color: var(--gray);
}

.form-group input:focus,
.form-group textarea:focus {
    outline: none;
    border-color: var(--primary-light);
    box-shadow: 0 0 0 3px rgba(99, 102, 241, 0.2);
}

.form-group textarea {
    min-height: 150px;
    resize: vertical;
}

/* Footer */
.footer {
    background: rgba(15, 23, 42, 0.9);
    padding: 3rem 0 1rem;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.footer-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin-bottom: 2rem;
}

.footer-logo {
    display: flex;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
}

.footer-logo img {
    width: 50px;
    height: 50px;
    border-radius: 12px;
    border: 2px solid var(--light);
}

.footer-logo p {
    font-family: 'Space Grotesk', sans-serif;
    font-weight: 600;
}

.footer-links {
    display: flex;
    grid-gap: 2rem;
    gap: 2rem;
    flex-wrap: wrap;
    justify-content: center;
}

.footer-links a {
    position: relative;
    transition: var(--transition);
    font-family: 'DM Sans', sans-serif;
    font-weight: 500;
}

.footer-links a::after {
    content: '';
    position: absolute;
    bottom: -5px;
    left: 0;
    width: 0;
    height: 2px;
    background: var(--primary-light);
    transition: var(--transition);
}

.footer-links a:hover::after {
    width: 100%;
}

.social-icons {
    display: flex;
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.social-icons a {
    font-size: 1.5rem;
    transition: var(--transition);
    color: var(--gray);
}

.social-icons a:hover {
    color: var(--primary-light);
    transform: translateY(-3px);
}

.footer-bottom {
    text-align: center;
    padding-top: 2rem;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    color: var(--gray);
    font-size: 0.9rem;
    font-family: 'DM Sans', sans-serif;
}

/* Responsive Design */
@media (max-width: 1200px) {
    .about-content {
        flex-direction: column;
    }
    
    .about-stats {
        width: 100%;
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (max-width: 992px) {
    .header-links {
        position: fixed;
        top: 0;
        left: -100%;
        width: 80%;
        height: 100vh;
        background: rgba(15, 23, 42, 0.95);
        backdrop-filter: blur(10px);
        -webkit-backdrop-filter: blur(10px);
        flex-direction: column;
        justify-content: center;
        align-items: center;
        transition: var(--transition);
        z-index: 99;
    }
    
    .header-links.open {
        left: 0;
    }
    
    .menu-toggle {
        display: flex;
    }
    
    .mobile-social {
        display: flex;
    }
    
    .contact-container {
        flex-direction: column;
    }
    
    .profile-info h1 {
        font-size: 2.5rem;
    }
    
    .profile-photo {
        width: 220px;
        height: 220px;
    }
}

@media (max-width: 768px) {
    .section {
        padding: 4rem 0;
    }
    
    .section-header h2 {
        font-size: 2rem;
    }
    
    .projects-grid {
        grid-template-columns: 1fr;
    }
    
    .profile-info h1 {
        font-size: 2rem;
    }
    
    .profile-info .title {
        font-size: 1rem;
    }
    
    .social-links a {
        width: 45px;
        height: 45px;
        font-size: 1.2rem;
    }
    
    .about-stats {
        grid-template-columns: repeat(2, 1fr);
    }
    
    .stat-item h3 {
        font-size: 2rem;
    }
}

@media (max-width: 480px) {
    .main-content {
        padding: 7rem 3% 0;
    }
    
    .profile-info h1 {
        font-size: 1.8rem;
    }
    
    .loading-text {
        font-size: 2rem;
    }
    
    .loading-spinner {
        width: 80px;
        height: 80px;
    }
    
    .about-actions {
        flex-direction: column;
    }
    
    .skills-container {
        grid-template-columns: 1fr;
    }
    
    .footer-links {
        flex-direction: column;
        align-items: center;
        grid-gap: 1rem;
        gap: 1rem;
    }
}
.chat-container {
  position: fixed;
  bottom: 30px;
  right: 30px;
  z-index: 1000;
}

.chat-toggle-button {
  background: linear-gradient(135deg, #6366f1, #8b5cf6);
  border: none;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  box-shadow: 0 4px 20px rgba(99, 102, 241, 0.3);
  transition: all 0.3s ease;
  position: relative;
}

.chat-toggle-button:hover {
  transform: scale(1.1);
  box-shadow: 0 6px 25px rgba(99, 102, 241, 0.4);
}

.chat-icon {
  color: white;
}

.unread-indicator {
  position: absolute;
  top: -5px;
  right: -5px;
  width: 12px;
  height: 12px;
  background: #ef4444;
  border-radius: 50%;
  border: 2px solid white;
}

.chat-board {
  position: absolute;
  bottom: 80px;
  right: 0;
  width: 350px;
  max-height: 500px;
  background: white;
  border-radius: 16px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.15);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  transform-origin: bottom right;
  animation: scaleIn 0.3s ease-out;
}

@keyframes scaleIn {
  from {
    transform: scale(0.8);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

.chat-header {
  background: linear-gradient(135deg, #6366f1, #8b5cf6);
  color: white;
  padding: 15px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.chat-header h2 {
  margin: 0;
  font-size: 1.2rem;
  font-weight: 600;
}

.chat-actions {
  display: flex;
  grid-gap: 15px;
  gap: 15px;
  align-items: center;
}

.close-chat, .clear-chat {
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  font-size: 1rem;
  transition: all 0.2s ease;
}

.clear-chat {
  opacity: 0.8;
  font-size: 0.9rem;
}

.clear-chat:hover {
  opacity: 1;
  text-decoration: underline;
}

.close-chat:hover {
  transform: rotate(90deg);
}

.messages-container {
  flex: 1 1;
  overflow-y: auto;
  padding: 15px;
  background: #f9fafb;
}

.messages {
  display: flex;
  flex-direction: column;
  grid-gap: 12px;
  gap: 12px;
}

.message {
  max-width: 80%;
  padding: 10px 15px;
  border-radius: 18px;
  position: relative;
  animation: fadeIn 0.3s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.message.user {
  align-self: flex-end;
  background: #6366f1;
  color: white;
  border-bottom-right-radius: 4px;
}

.message.bot {
  align-self: flex-start;
  background: #e5e7eb;
  color: #111827;
  border-bottom-left-radius: 4px;
}

.message-content {
  word-wrap: break-word;
  white-space: pre-wrap;
}

.timestamp {
  display: block;
  font-size: 0.7rem;
  opacity: 0.7;
  margin-top: 4px;
  text-align: right;
}

.message.bot .timestamp {
  text-align: left;
}

.copy-button {
  position: absolute;
  top: 5px;
  right: 5px;
  background: rgba(0, 0, 0, 0.1);
  border: none;
  border-radius: 50%;
  width: 25px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  opacity: 0;
  transition: all 0.2s ease;
  color: #4b5563;
}

.message.bot:hover .copy-button {
  opacity: 1;
}

.copy-button:hover {
  background: rgba(0, 0, 0, 0.2);
}

.typing-indicator {
  display: flex;
  grid-gap: 5px;
  gap: 5px;
  padding: 10px 0;
}

.typing-indicator span {
  width: 8px;
  height: 8px;
  background: #9ca3af;
  border-radius: 50%;
  display: inline-block;
  animation: bounce 1.4s infinite ease-in-out;
}

.typing-indicator span:nth-child(1) {
  animation-delay: 0s;
}

.typing-indicator span:nth-child(2) {
  animation-delay: 0.2s;
}

.typing-indicator span:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes bounce {
  0%, 60%, 100% {
    transform: translateY(0);
  }
  30% {
    transform: translateY(-5px);
  }
}

.empty-state {
  text-align: center;
  padding: 30px 0;
  color: #6b7280;
  font-size: 0.9rem;
}

.message-input {
  display: flex;
  padding: 15px;
  background: white;
  border-top: 1px solid #e5e7eb;
}

.message-input input {
  flex: 1 1;
  padding: 12px 15px;
  border: 1px solid #e5e7eb;
  border-radius: 25px;
  outline: none;
  font-size: 0.9rem;
  transition: all 0.3s ease;
}

.message-input input:focus {
  border-color: #6366f1;
  box-shadow: 0 0 0 3px rgba(99, 102, 241, 0.2);
}

.send-button {
  background: #6366f1;
  border: none;
  border-radius: 50%;
  width: 45px;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 10px;
  cursor: pointer;
  transition: all 0.3s ease;
  color: white;
}

.send-button:disabled {
  background: #e5e7eb;
  color: #9ca3af;
  cursor: not-allowed;
}

.send-button.active:hover {
  background: #4f46e5;
  transform: translateY(-2px);
}

@media (max-width: 480px) {
  .chat-container {
    bottom: 20px;
    right: 20px;
  }
  
  .chat-board {
    width: calc(100vw - 40px);
    max-height: 70vh;
    bottom: 70px;
    right: 20px;
  }
}
